import React from "react";
import styled from "@emotion/styled";
import { BrowserRouter as Router, Route, Link, Switch, Redirect } from "react-router-dom";

import Activity from "../molecules/Routers/Activity";
import Clubs from "../molecules/Routers/Clubs";
import Board from "../molecules/Routers/Board";
import BoardVoivodeship from "../molecules/Routers/BoardVoivodeship";
import Field from "../molecules/Routers/Field";
import Referees from "../molecules/Routers/Referees";
import Rules from "../molecules/Routers/Rules";

const Content = () => {
    return (
        <StyledWrapper>
            <Switch>
                <Route exact path="/handball_site/" component={Activity} />
                <Route path="/handball_site/clubs" component={Clubs} />
                <Route path="/handball_site/board" component={Board} />
                <Route path="/handball_site/boardvoivo" component={BoardVoivodeship} />
                <Route path="/handball_site/field" component={Field} />
                <Route path="/handball_site/referees" component={Referees} />
                <Route path="/handball_site/rules" component={Rules} />
            </Switch>
        </StyledWrapper>
    )
};

const StyledWrapper = styled.div`
    
`;

export default Content;